<script>
import UtilsMixin from './mixins/utils';
export default {
  data(){
    return {
      authToken: localStorage.getItem('authToken'),
      schema_page: 1,
      authRoutes:  ['register', 'PKIIdLogin'],
      menu: [
        { 
          name: "Dashboard",  
          path: "/studio/dashboard",
          isShow: true,
        },
        { 
          name: "Schema",  
          path: "/studio/schema",
          isShow: true,
        },
        { 
          name: "Credentials",  
          path: "/studio/credential",
          isShow: true,
        },
        { 
          name: "Presentation",  
          path: "/studio/presentation",
          isShow: true,
        },
        {
          name: "Logout",
          path: "/login",
          isShow: false,
        },        
      ]
    }
  },
  async mounted() {
    console.log('Initiating mounted with schema and credentials');
    this.getList('SCHEMA')
    this.getList('CREDENTIAL')
    this.fetchTemplates();
  },
  methods: {
    vcStatus(vcId){
      return fetch(vcId+':')
      .then(resp => {
        return resp.json()
      }).then(data => {
        return data
      }).catch(e => {
        Promise.reject(e.message)
      })
    },
    fetchTemplates() {
      const url = `${this.$config.studioServer.BASE_URL}api/v1/presentation/template`
      const headers = {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${this.authToken}`

      }
      fetch(url, {
        headers
      }).then(response => response.json()).then(json => {
        json.forEach(template => {
          this.$store.commit('insertApresentationTemplate', template)
        })
      })
    },
    async getList(type) {
      let url = "";
      let options = {}
      if (type === "SCHEMA") {
        url = `${this.$config.studioServer.BASE_URL}${this.$config.studioServer.SCHEMA_LIST_EP}?page=${this.schema_page}&limit=10`

        options = {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${this.authToken}`
          }
        }
      } else {
        url = `${this.$config.studioServer.BASE_URL}${this.$config.studioServer.CRED_LIST_EP}`;
        options = {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${this.authToken}`
          }
        }
      }

      const resp = await fetch(url, options);
      const j = await resp.json();
      if (j && j.status == 500) {
        return this.notifyErr(`Error:  ${j.error}`);
      }
      if (type === "SCHEMA") {
        console.log(j);
        const schemaList = j.schemaList
        schemaList.forEach(schema => {
          this.$store.dispatch('insertAschema', schema)
        })
      } else {
        j.credList.forEach(credential => {
          this.$store.dispatch('insertAcredential', credential)
        })
      }
    },
    logout(){
      localStorage.removeItem('authToken')
      localStorage.removeItem('user')
      localStorage.removeItem("credentials")
      localStorage.removeItem("userData")
    },
    goToNextPage(route){
      const r = this.menu.find(x => x.name === route)
      if(r.name === "Logout") this.logout()
      this.$router.push(r.path)
      if(this.$route.params.nextUrl != null){
                    this.$router.push(this.$route.params.nextUrl)
                }else{
        this.$router.push(r.path)
                }
    }
  },
  mixins: [UtilsMixin]
}
</script>